@import "./base/nule.css";
@import "./base/fonts.css";

body {
   background-color: #ffdf8c;
   padding: 43px;
   font-family: "proximanova", sans-serif;
   font-weight: 400;
}
@media (max-width: 830px) {
   body {
      padding: 20px;
   }
}
@media (max-width: 500px) {
   body {
      padding: 10px;
   }
}

.wrapper {
   background-color: #ffffff;
   width: 100%;
   height: 100%;
   border-radius: 10px;
}
.container {
   max-width: 1225px;
   margin: 0 auto;
   padding: 0 20px;
}
