.root {
   text-align: center;
   padding: 150px 0;
   display: flex;
   flex-direction: column;
   align-items: center;
   max-width: 600px;
   margin: 0 auto;
}
.title {
   font-weight: 700;
   font-size: 32px;
   line-height: 39px;
   margin-bottom: 10px;

   letter-spacing: 0.01em;
}
.title_block {
   display: flex;
   flex-direction: row-reverse;
   align-items: center;
   /*justify-content: center;*/
   column-gap: 15px;
}
.smile {
   width: 36px;
}
.subtitle {
   color: #777777;
   margin-bottom: 47px;
   font-size: 18px;
   line-height: 145.4%;
   letter-spacing: 0.01em;
}
