.root {
   display: flex;
   column-gap: 15px;
   align-items: center;
   font-size: 20px;
}
.root li {
   border-radius: 50%;
   border: 1px solid #eb5a1e;
   transition: 0.3s all ease-in-out;
}
.root li:hover:not(:global(.next), :global(.previous), :global(.selected)) {
   background-color: #eb5a1e;
   transform: scale(1.08);
}
.root li:hover:not(:global(.next), :global(.previous)) a {
   color: white;
}
.root a {
   display: block;
   color: #eb5a1e;
   cursor: pointer;

   width: 40px;
   height: 40px;
   display: flex;
   align-items: center;
   justify-content: center;
}
:global(.selected) {
   background-color: #eb5a1e;
}
:global(.selected) a {
   color: white;
}

:global(.previous):hover {
   border: 1px solid #f58d60;
}
:global(.previous) a:hover {
   color: #f58d60;
}
:global(.next):hover {
   border: 1px solid #f58d60;
}
:global(.next) a:hover {
   color: #f58d60;
}
:global(.next) a,
:global(.previous) a {
   width: 30px;
   height: 30px;
   font-size: 14px;
}
