.main {
}
.container {
}
.main__container {
   display: flex;

   flex-direction: column;
}

.main__nav-block {
   padding: 40px 0;
   font-weight: 700;
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
   column-gap: 20px;
   row-gap: 20px;
   align-items: center;
}
.nav-block-main {
}
.nav-block-main__assortiment {
   width: 70%;
   overflow: hidden;
}
.nav-block-main__assortiment ::-webkit-scrollbar {
   width: 0;
}
@media (max-width: 1100px) {
   .nav-block-main__assortiment {
      width: 100%;
   }
}
.main__shop {
   padding-bottom: 50px;
}
.main-shop {
}
.main-shop__title {
   font-size: 37px;
   font-weight: 700;
}
.main-shop__items {
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
   row-gap: 65px;
   column-gap: 35px;
   padding: 50px 0;
   /*justify-content: space-evenly;*/
}
