.nav-block-main__items {
   display: flex;
   gap: 9px;
   /*flex-wrap: wrap;*/
   overflow: auto;
   align-items: center;
}
.nav-block-main__items ::-webkit-scrollbar {
   width: 0;
}
.nav-block-main__item {
   padding: 14px 25px;
   background-color: #f9f9f9;
   border-radius: 30px;
   transition: 0.2s all ease-in-out;
   cursor: pointer;
}
.nav-block-main__item.active {
   background-color: #282828;
   color: white;
}
.nav-block-main__item:hover {
   background-color: #f3f3f3;
}
.nav-block-main__item.active:hover {
   background-color: #000000;
}
