.header {
   font-size: 16px;
   border-bottom: 1px solid #f6f6f6;
}
.header__container {
   display: grid;
   grid-template-columns: 237px 0.5fr 5fr 2fr 160px;
   justify-content: space-between;
   align-items: center;
   column-gap: 20px;
   padding: 50px 20px;
}
@media (max-width: 1044px) {
   .header__container {
      grid-template-columns: 237px 0.5fr 6fr 1fr 160px;
   }
}
@media (max-width: 814px) {
   .header__container {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      row-gap: 15px;
   }
}
@media (max-width: 585px) {
   .header__container {
      flex-direction: column;
      justify-content: unset;
      row-gap: 20px;
      align-items: flex-start;
      padding: 30px 20px;
   }
}

.logo-block-header {
   display: flex;
   /*padding: 50px 0;*/
   column-gap: 17px;
   align-items: center;
   grid-column-start: 1;
}

.logo-block-header__link {
}
.logo-block-header__img {
   width: 100%;
}
.logo-block-header__text {
   display: flex;
   flex-direction: column;
}

.logo-block-header__title {
   font-weight: 800;
   font-size: 24px;
}
.logo-block-header__desc {
}
.header__cart-info-block {
}
.cart-info-block {
   background-color: #fe5f1e;
   border-radius: 30px;
   color: white;
   font-weight: 700;
   display: flex;
   align-items: center;
   overflow: hidden;
   grid-column-start: 5;
   justify-self: flex-end;
}

.cart-info-block__link-price {
   color: white;
   flex: 0 0 50%;
   display: flex;
   justify-content: center;
   white-space: nowrap;
   padding: 15px 13px 15px 23px;
   transition: 0.2s all ease-in-out;
}
.cart-info-block__link-price:hover {
   background-color: #ff8c5e;
}
.cart-info-block__link-number {
   color: white;
   flex: 0 0 50%;
   display: flex;
   justify-content: center;
   align-items: center;
   column-gap: 8px;
   padding: 15px 30px 15px 13px;
   transition: 0.2s all ease-in-out;
   /*width: 100%;*/
}
.cart-info-block__link-number-span {
   /*margin-right: 30px;*/
}
.cart-info-block__link-number:hover {
   background-color: #ff8c5e;
}
