.main__sort-block {
   display: flex;
   align-items: center;
}
.sort-block-main {
   position: relative;
}
.sort-block-main__img {
   margin-right: 7px;
   transition: 0.5s all ease-in-out;
}
.sort-block-main__img.active {
   transform: rotate(180deg);
}
.sort-block-main__title {
   margin-right: 8px;
   font-weight: 700;
}
a.sort-block-main__link {
   color: #fe5f1e;
   border-bottom: 1px dashed #fe5f1e;
   text-decoration: none;
}
.sort-block-main__link:hover {
   border-bottom: unset;
}
.sort-block-main__link:active {
   color: #e74d10;
}
.change-sort {
   position: absolute;
   top: 150%;
   right: 0;
   background-color: #fff;
   box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
   border-radius: 10px;
   font-size: 14px;
   font-weight: 400;
   padding: 13px 0 9px 0;
   z-index: 3;
   visibility: hidden;
   opacity: 0;
   transition: 0.4s all ease-in-out;
   white-space: nowrap;
   width: 200px;
}
.change-sort.active {
   visibility: visible;
   opacity: 1;
}
.change-sort__items {
}
.change-sort__item {
   padding: 10px 22px 10px 14px;
   cursor: pointer;
}
.change-sort__item.active {
   background-color: #fff8f2;
   color: #fe5f1e;
   font-weight: 700;
}
.change-sort__item:hover {
   background-color: #fff8f2;
}
