.root {
   max-width: 400px;
   grid-column-start: 3;
   display: flex;
   border: 1px solid rgb(0, 0, 0, 0.1);
   padding: 12px 30px;
   column-gap: 10px;
   border-radius: 16px;
}

.input {
   width: 100%;
}
.input::placeholder {
   color: rgb(190, 190, 190);
}
.input:focus::placeholder {
   color: rgb(139, 139, 139);
}
.img {
   width: 20px;
}
.input:hover::placeholder {
   color: rgb(139, 139, 139);
}
.clear {
   width: 20px;
   cursor: pointer;
}
