.item-main-shop__img {
   margin-bottom: 11px;
   max-width: 300px;
   align-self: center;
}
.main-shop__item {
   display: flex;
   flex-direction: column;
   align-items: center;
   text-align: center;
   /*align-items: center;*/
   /*max-width: 280px;*/
}
.main-shop__item:hover {
   transform: translateX(-3px);
   transition: 0.4s all ease-in-out;
}
.main-shop__item:not(:hover) {
   transition: 0.4s all ease-in-out;
}
.item-main-shop {
}
.item-main-shop__title {
   font-weight: 800;
   font-size: 25px;
   margin-bottom: 22px;
   align-self: center;
}
.item-main-shop__modification {
   background-color: #f3f3f3;
   border-radius: 10px;
   width: 100%;
   padding: 7px;
   display: flex;
   flex-direction: column;
   row-gap: 7px;
   margin-bottom: 17px;
}

.modification {
}
.modification__up {
   display: grid;
   column-gap: 5px;
   grid-template-columns: repeat(auto-fit, minmax(0px, 1fr));
}
.modification__up.modification__btn {
}

.modification__btn {
   display: flex;
   justify-content: center;
   align-items: center;
   padding: 10px 0;
   box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
   font-size: 14px;
   font-weight: 700;
   border-radius: 5px;
   transition: 0.1s all ease-in-out;
}
.modification__btn:hover {
   background-color: #f8f8f8;
}
.modification__btn:active {
   background-color: #ffffff;
}
.modification__btn.active {
   background-color: #fff;
}
.active {
}
.modification__down {
   column-gap: 5px;
   display: grid;
   grid-template-columns: repeat(auto-fit, minmax(0px, 1fr));
}
.modification__btn {
   /*flex: 0 0 33.333%;*/
}

.item-main-shop__cta-block {
   display: flex;
   justify-content: space-between;
   align-items: center;
   column-gap: 5px;
   width: 100%;
}
.item-main-shop__price {
   font-weight: 700;
   font-size: 22px;
}
.item-main-shop__btn {
   border: 1px solid #eb5a1e;
   display: flex;
   align-items: center;
   padding: 12px 17px;
   border-radius: 30px;
   column-gap: 8px;
   font-weight: 700;
   font-size: 16px;
   color: #eb5a1e;
   transition: 0.3s all ease-in-out;
}

.item-main-shop__btn:hover {
   background-color: #eb5a1e;
   color: white;
}

.item-main-shop__btn:hover svg path {
   fill: white;
}
.item-main-shop__btn:hover span {
   background-color: #fff;
   color: #eb5a1e;
}
.item-main-shop__btn:active {
   background-color: #e05113;
}

.item-main-shop__number {
   width: 22px;
   height: 22px;
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 50%;
   background-color: #eb5a1e;
   color: white;
   padding: 3px;
   font-size: 13px;
}
