.root {
   text-align: center;
   padding: 100px 0;
}
.title {
   font-weight: 700;
   font-size: 32px;
   line-height: 39px;
   margin-bottom: 10px;

   letter-spacing: 0.01em;
}
.title_block {
   display: flex;
   align-items: center;
   /*justify-content: center;*/
   column-gap: 10px;
}
.smile {
   width: 32px;
}
.subtitle {
   color: #777777;
   margin-bottom: 47px;
   font-size: 18px;
   line-height: 145.4%;
   letter-spacing: 0.01em;
}
.img_block {
   max-width: 300px;
}
.img {
   width: 100%;
   margin-bottom: 44px;
}
a.btn {
   background-color: #282828;
   border-radius: 30px;
   padding: 14px 35px;
   color: white;
}
.btn:hover {
   background-color: #0f0f0f;
}
.btn:active {
   background-color: #282828;
}
.container {
   max-width: 550px;
   display: flex;
   flex-direction: column;
   margin: 0 auto;
   align-items: center;
}
